.nav {
  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    position: relative;

    color: var(--main-color);
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &::after {
      content: '';
      position: absolute;
      bottom: -8px;

      display: block;
      height: 1px;
      width: 100%;

      background-color: var(--main-color);

      transition: transform $effectDuration;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}
